.sidebar{
    display: flex;
    flex-direction: column;
    flex:0.35;
}


.sidebar_header{
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-right: 1px solid lightgrey;
}
.sidebar_headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}
.sidebar_headerRight .MuiSvgIcon-root {
    /* margin-right: 2vw; */
    font-size: 22px !important;
}


.sidebar_search{    
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height:39px;
    padding: 10px;
}
.sidebar_searchContainer{    
    display: flex;
    align-items: center;
    background-color: #fff;
    width:100%;
    height:35px;
    border-radius: 20px;
}
.sidebar_searchContainer > input{
    border: none;
    margin-left: 20px;
}
.sidebar_searchContainer > input:focus{
    outline: none;
    margin-left: 20px;
}
.sidebar_searchContainer  .MuiSvgIcon-root {
    color: grey;
    padding-top: 3px;
    padding-left: 10px;
}


.sidebar_chat{
    flex: 1;
    background-color: white;
    overflow: scroll;
    /* padding: 10px; */
}